import {
    GoogleMap,
    withScriptjs,
    withGoogleMap,
    Marker,
    InfoWindow,
} from 'react-google-maps';

const GMap = (props) => {
    console.log(props);
    return (
        <GoogleMap
            defaultCenter={{ lat:40.712776, lng:-74.005974 }}
            defaultZoom={11}
            center={props.position}
        >
            <Marker key={props.key} position={props.position} />
            <InfoWindow
                position={props.position}
            >
                <div>
                    {props.name}
                </div>
            </InfoWindow>
        </GoogleMap>
    )
}

export default withScriptjs(withGoogleMap(GMap));